<template>
    <div class="table-wrapper" @click="tickTimer">
        <TransitionGroup
            tag="div"
            name="fade"
            class="table"
        >
            <div
                v-for="item in itemsList"
                :key="item.id"
                :class="item.class"
                class="table-item"
            >
                <div
                    v-if="['a','b','ba'].includes(item.type)"
                ></div>
                <span
                    v-if="item.type==='header'"
                >{{item.label}}</span>
            </div>

        </TransitionGroup>
    </div>
</template>

<script>
export default {
    props: [
        'term1',
        'term2',
        'sign',
    ],
    data() {
        return {
            itemsList: [],
            sortStep: null,
            sortIncrement: null,
            setTimeout: null,
        }
    },
    created() {
        this.$watch(
            () => this.term1 + this.sign + this.term2,
            this.rebuild
        )
        this.rebuild()
    },
    mounted() {
        this.tickTimer();
    },
    beforeUnmount() {
        if (this.setTimeout) {
            clearTimeout(this.setTimeout);
    }
    },
    methods: {
        tickTimer() {
            if (this.setTimeout) {
                this.sort();
            }
            if (this.setTimeout) {
                clearTimeout(this.setTimeout);
            }
            this.setTimeout = setTimeout(() => {
                this.timer += 1
                this.tickTimer()
            }, this.setTimeout ? 4000 : 1000)
        },
        rebuild() {
            this.buildItemsList(
                this.term1 < this.term2 && this.sign === 'plus' ? this.term2 : this.term1,
                this.term1 < this.term2 && this.sign === 'plus' ? this.term1 : this.term2,
            )
            if (this.sign === 'plus') {
                this.sortStep = 0
                this.sortIncrement = 1
            } else {
                this.sortStep = 2
                this.sortIncrement = -1
            }
            this.sort()
        },
        calculate() {
            let sum = 0
            for (let i = this.itemsList.length - 1; i >= 0; i--) {
                if (this.itemsList[i].type === 'header') {
                    this.itemsList[i].label = sum || null;
                    sum = 0
                    continue
                }
                sum += ['a','b','ba'].includes(this.itemsList[i].type)
            }
        },
        generateItemsList(sort, list) {
            let id = 0;
            for(const item of list) {
                for(let i = 0; i < item.num; i++) {
                    if (id % 11 === 0) {
                        this.addListItem(id, id, sort, {
                            type: 'header',
                            from: 0,
                        })
                        id++
                    }
                    this.addListItem(id, i, sort, item)
                    id++
                }
            }
        },
        addListItem(id, i, sort, item) {
            if (this.itemsList[id]) {
                this.itemsList[id].sort[sort] = item.from + i + (item.type !== 'header' ? Math.floor((item.from + i)/10) + 1 : 0)
            } else {
                this.itemsList.push({
                    id: id,
                    type: item.type,
                    sort: {
                        0: id,
                        [sort]: item.from + i + (item.type !== 'header' ? Math.floor((item.from + i)/10) + 1 : 0)
                    },
                    class: {
                        'item-a': item.type === 'a',
                        'item-b': item.type === 'b' || item.type === 'ba',
                        'item-header': item.type === 'header'
                    },
                    label: null,
                });
            }
        },
        buildItemsList(a, b) {
                let e = {}
                e.a = a
                e.am = (10 - e.a % 10) % 10
                e.ab = b < e.am ? b : e.am
                e.a0 = e.am-e.ab
                e.b = b > e.am ? b - e.am : 0
                e.bb = (10 - (e.b + e.ab + e.a0) % 10) % 10 > e.ab ? e.ab : (10 - (e.b + e.ab + e.a0) % 10) % 10
                e.b0 = (10 - (e.b + e.ab + e.bb + e.a0) % 10) % 10
                // console.log(e)

                this.itemsList = []

                this.generateItemsList(1, [
                    {from: 0, num: e.a, type: 'a'},
                    {from: e.a, num: e.ab, type: null}, //1
                    {from: e.a+e.ab, num: e.a0, type: null},
                    {from: e.a+e.ab+e.a0, num: e.b, type: 'b'},
                    {from: e.a+e.ab+e.a0+e.b+e.bb+e.b0, num: e.ab, type: 'ba'}, //3
                    {from: e.a+e.ab+e.a0+e.b+e.bb, type: null, num: e.b0},
                    {from: e.a+e.ab+e.a0+e.b, num: e.bb, type: null}, //2
                    {from: e.a+2*e.ab+e.a0+e.b+e.b0+e.bb, type: null, num: e.a0},
                ]);

                this.generateItemsList(2, [
                    {from: 0, num: e.a, type: 'a'},
                    {from: e.a+e.ab+e.a0+e.b+e.bb+e.b0, num: e.ab, type: 'ba'}, //3
                    {from: e.a+e.ab, num: e.a0, type: null},
                    {from: e.a+e.ab+e.a0, num: e.b, type: 'b'},
                    {from: e.a, num: e.ab, type: null}, //1
                    {from: e.a+e.ab+e.a0+e.b+e.bb, type: null, num: e.b0},
                    {from: e.a+e.ab+e.a0+e.b, num: e.bb, type: null}, //2
                    {from: e.a+2*e.ab+e.a0+e.b+e.b0+e.bb, type: null, num: e.a0},
                ]);

        },
        sort() {
            // console.log('shuffle')
            this.itemsList = this.itemsList.sort((a, b) => a.sort[this.sortStep] - b.sort[this.sortStep])
            this.sortStep = (this.sortStep + this.sortIncrement + 3) % 3
            this.calculate()
        }
    }
}
</script>

